import React, { useState, useRef, useCallback, memo } from 'react';
import LayoutDivider from '~/components/LayoutDivider';
import LayoutSort from '~/layouts/LayoutSort';
import { getRandomNoDuplicateArray, sleep } from '~/utils/regular';
const MemoChild = memo(LayoutSort);
const QuickSort = () => {
    console.log('QuickSort Rerender');
    const refArray = useRef(getRandomNoDuplicateArray(80));
    const [tempArray, setTempArray] = useState(refArray.current);
    const BoxDatas = useCallback(() => refArray.current, [refArray.current]);
    const onStart = async () => {
        await quickSort(refArray.current);
    };
    const quickSort = async (array, length = array.length - 1, start = 0) => {
        if (array.length < 2)
            return array; // base case
        let left = [];
        let right = [];
        let pivot = array[length];
        while (start < length) {
            if (array[start] < pivot) {
                left.push(array[start]);
            }
            else {
                right.push(array[start]);
            }
            start++;
        }
        refArray.current = replaceOrignalPartArray(refArray.current, pivot, [
            ...left,
            pivot,
            ...right,
        ]);
        setTempArray(refArray.current);
        await sleep(100);
        return [...(await quickSort(left)), pivot, ...(await quickSort(right))];
    };
    const replaceOrignalPartArray = (orgArray, pivot, changeArray) => {
        const temp = new Array(...orgArray);
        temp.splice(temp.findIndex((i) => i === pivot) - (changeArray.length - 1), changeArray.length, ...changeArray);
        return temp;
    };
    const content = {
        desktop: () => (React.createElement(LayoutSort, { title: "QuickSort", isDesktop: true, boxDatas: refArray.current, onStart: onStart })),
        mobile: () => (React.createElement(LayoutSort, { title: "QuickSort", isDesktop: false, boxDatas: refArray.current, onStart: onStart })),
    };
    return React.createElement(LayoutDivider, { ...content });
};
export default QuickSort;
